import React from "react";
import {
  Card,
  CardTitle,
  CardText,
  IconButton,
  CardActions,
  Button,
  CardMenu,
} from "react-mdl";
import { motion } from "framer-motion";
function ReactProject() {
  return (
    <motion.div
      initial={{ opacity: 0, x: "-100%" }}
      animate={{ opacity: 1, x: 0 }}
      exit={{ opacity: 0, x: "-100%" }}
      className="project_container"
    >
      <Card shadow={1} style={{ width: "400px", margin: "auto" }}>
        <CardTitle
          style={{
            color: "#fff",
            height: "176px",
            background:
              "url(https://res.cloudinary.com/practicaldev/image/fetch/s--MzcqFejf--/c_limit%2Cf_auto%2Cfl_progressive%2Cq_auto%2Cw_880/https://curatti.com/wp-content/uploads/2017/11/ReactJS-1000x500.jpg) center / cover",
          }}
        >
          React Project #1
        </CardTitle>
        <CardText>
          The Project Consist of multiple single page applications.That consume
          a foreign API and display the result to the client. Live Weather App,
          Live Recipe App, Live currency conversion app etc. The project uses
          redux,react-routing,APIs etc.
        </CardText>
        <CardActions border>
          <Button colored>
            <a target="_blank" href="https://samul333.github.io/testapiapp/">
              Live Demo
            </a>
          </Button>
        </CardActions>
        <CardMenu style={{ color: "#fff" }}>
          <IconButton name="share" />
        </CardMenu>
      </Card>

      <Card shadow={0} style={{ width: "400px", margin: "auto" }}>
        <CardTitle
          style={{
            color: "#fff",
            height: "176px",
            background:
              "url(https://res.cloudinary.com/practicaldev/image/fetch/s--MzcqFejf--/c_limit%2Cf_auto%2Cfl_progressive%2Cq_auto%2Cw_880/https://curatti.com/wp-content/uploads/2017/11/ReactJS-1000x500.jpg) center / cover",
          }}
        >
          React Project #2
        </CardTitle>
        <CardText>
          Corona Tracking Website. Information of corona counts all over the
          world.
        </CardText>
        <CardActions border>
          <Button colored>
            <a target="_blank" href="https://samul333.github.io/ronatracker/">
              Live Demo
            </a>
          </Button>
        </CardActions>
        <CardMenu style={{ color: "#fff" }}>
          <IconButton name="share" />
        </CardMenu>
      </Card>
      <Card shadow={0} style={{ width: "400px", margin: "auto" }}>
        <CardTitle
          style={{
            color: "#fff",
            height: "176px",
            background:
              "url(https://res.cloudinary.com/practicaldev/image/fetch/s--MzcqFejf--/c_limit%2Cf_auto%2Cfl_progressive%2Cq_auto%2Cw_880/https://curatti.com/wp-content/uploads/2017/11/ReactJS-1000x500.jpg) center / cover",
          }}
        >
          React Project #3
        </CardTitle>
        <CardText>
          Replication of a mobile news portal website using React, Firebase.
        </CardText>
        <CardActions border>
          <Button colored>
            <a target="_blank" href="https://nba-full-4e8ba.firebaseapp.com/">
              Live Demo
            </a>
          </Button>
        </CardActions>
        <CardMenu style={{ color: "#fff" }}>
          <IconButton name="share" />
        </CardMenu>
      </Card>
      <Card shadow={0} style={{ width: "400px", margin: "auto" }}>
        <CardTitle
          style={{
            color: "#fff",
            height: "176px",
            background:
              "url(https://res.cloudinary.com/practicaldev/image/fetch/s--MzcqFejf--/c_limit%2Cf_auto%2Cfl_progressive%2Cq_auto%2Cw_880/https://curatti.com/wp-content/uploads/2017/11/ReactJS-1000x500.jpg) center / cover",
          }}
        >
          React Project #4
        </CardTitle>
        <CardText>Replication of Netflix using React</CardText>
        <CardActions border>
          <Button colored>
            <a target="_blank" href="https://netflix-clone-222b9.web.app/">
              Live Demo
            </a>
          </Button>
        </CardActions>
        <CardMenu style={{ color: "#fff" }}>
          <IconButton name="share" />
        </CardMenu>
      </Card>
    </motion.div>
  );
}

export default ReactProject;
