import React from 'react'
import {Grid,Cell} from 'react-mdl'
import {HtmlData,PyData,mongoData,unityData,JavaData,ReactData} from '../Data/Data'
import {motion} from 'framer-motion'
function LandingPage() {
    return (
        <motion.div initial={{opacity:0}}
        animate={{opacity:1}}
        exit={{opacity:0}}
        style={{width:'100vw', margin:'auto'}}>
            <Grid className="landing-grid">
                <Cell col={12}>
                    <div className="banner-img">
                <img src="https://fiverr-res.cloudinary.com/images/t_main1,q_auto,f_auto,q_auto,f_auto/gigs/125296371/original/653cc81872119844644e33d40f5afd9bd61743b6/create-cool-cartoon-avatars.jpg"
                alt="avatar" className="avatar-img"/>
                </div>

                    <div className="banner-text">
                        <div className="banner-header">
                        <h1>Skill Tree</h1>
                        <hr/>
                        </div>
                    <p>HTML5/CSS3 | Bootstrap | JavaScript | Firebase | React | NodeJs | Express | MongoDB |Java | Andriod | C# | Unity | Python </p>
                   <hr/>
                   <h2>Hover Icon for Experience Info</h2>
                    </div>
                      
                    <div className ="social-links">

                        <div className="html" data-tooltip={HtmlData}><img  src="https://image.flaticon.com/icons/svg/143/143655.svg" alt="html"/></div>
                        <div className="html" data-tooltip={PyData}> <img src="https://www.flaticon.com/premium-icon/icons/svg/3098/3098090.svg"/> </div>
                        <div className="html" data-tooltip={ReactData}><img src="https://www.flaticon.com/premium-icon/icons/svg/1183/1183672.svg"/> </div>
                        <div className="html" data-tooltip={JavaData}> <img src="https://image.flaticon.com/icons/svg/919/919854.svg"/> </div>
                        
                        <div className="html" data-tooltip={mongoData}>  <img src="https://image.flaticon.com/icons/svg/919/919828.svg"/></div>
                        <div className="html" data-tooltip={unityData}>   <img src="https://upload.wikimedia.org/wikipedia/commons/8/8a/Official_unity_logo.png"/></div>
                        


                    </div>
                </Cell>

            </Grid>
            
        </motion.div>
    )
}

export default LandingPage
