import React from 'react'

function FAQ({item,index,toggleFAQ}) {
    
    return (
      
  <div className = {"faq "+ (item.open? 'open':'')}
        onClick={()=>toggleFAQ(index)}
        >

                <div className="faq-question">
                    {item.question}
                </div>

                <div className="faq-answers">
                    {item.answer}
                </div>
            
        </div>
    )
}

export default FAQ
