import React,{useState} from 'react'
import { Grid, Cell } from 'react-mdl'
import Education from './Education'
import Experience from './Experience'
import Skills from './Skills'
import FAQ from './FAQ'
import {motion} from 'framer-motion'
function Resume() {
    const [faqs,setFaqs] = useState([
        {
            question:'How many programmer does it take to change a light bulb?',
            answer:'None, we do not adress hardware issues ',
            open:false
        },
        {
            question:'Best source of Infromation?',
            answer:'Stack overflow ',
            open:false
        },
    ]);

    const toggleFAQ = index =>{
       setFaqs(faqs.map((faq,i)=>{
           if(i===index){
               faq.open=!faq.open
           }else{
              
           }
           return faq;
       }))
    }


    return (
        <motion.div
        initial={{opacity:0}}
        animate={{opacity:1}}
        exit={{opacity:0}}
        >
            <Grid>
                <Cell col={4}>
                    <div className="resume-col-left">
                        <img src="https://fiverr-res.cloudinary.com/images/t_main1,q_auto,f_auto,q_auto,f_auto/gigs/125296371/original/653cc81872119844644e33d40f5afd9bd61743b6/create-cool-cartoon-avatars.jpg"

                            alt="avatar"
                            style={{ height: '150px', width: 'auto', borderRadius: '5px' }} />
                        <h3>Samul Shrestha</h3>

                        <p>
                        I am a Samul Shrestha who aims to be able to find an exciting and challenging entry level position in the Information and Technology industry alongside a company who will continuously motivate and drive me to do my best and improve on my skills and abilities in order to be able to assist the company in achieving its company mission and goals.
                        I have always been interested in working with new Technologies.     
                        </p>
                    </div>
               
                       <h3> 
                       Some FAQs
                       <hr style={{width:'50%', opacity:1, color:'black'}}/>
                                </h3>
                            <div className="faqs">
                                {faqs.map((item,i)=>{
                                    return<div key={i}><FAQ item={item} index={i} toggleFAQ={toggleFAQ}/></div>
                                })}
                            </div>
                     
                   
                    
                </Cell>

                <Cell col={8} className="resume-right-col">
                    <h2>Education</h2>
                    <hr style={{width:'50%'}}/>
                    <Education
                    start={2003}
                    end={2013}
                    name={'WhiteField Higher Secondary School'}
                    post={'SLC'}/>
                     <Education
                    start={2013}
                    end={2015}
                    name={'Xavier Academy,Laximpat'}
                    post={'Intermediate Level +2,Science'}/>
                     <Education
                    start={2016}
                    end={2020}
                    name={'Prime College'}
                    post={'B.Sc. Computer Science and Information Technology'}/>

                <h2>Experience </h2>
                <hr style={{width:'50%'}}/>

                <Experience
                start="2018"
                end="2019"
                duration="3 Months"
                companyName="Financial Controller Goverment Office"
                jobDescription={`I have worked on FCGO's IT department for 3 months as an intern. The responsibilities
                assigned to me during the period of internship was to learn and update the systems that were
                on use there. Some of the key technologies that I have learned from the experience are Java, Node, JavaScript and many more.`}/>

                <h2>Skills</h2>
                <hr style={{width:'50%'}}/>
                <Skills
                skills="Java"
                progress={50}/>
                <Skills
                skills="ReactJs"
                progress={85}/>
                <Skills
                skills="HTML/CSS"
                progress={85}/>
                <Skills
                skills="NodeJs"
                progress={80}/>
                <Skills
                skills="MongoDB"
                progress={85}/>
                <Skills
                skills="Unity"
                progress={40}/>
                <Skills
                skills="Python"
                progress={50}/>
                   <Skills
                skills="JavaScript"
                progress={75}/>
                  <Skills
                skills="React Native"
                progress={25}/>
                
                </Cell>

            </Grid>
        </motion.div>
    )
}

export default Resume
