import React, { Component } from 'react'
import {Grid,Cell} from 'react-mdl'

export class Education extends Component {
    render() {
        return (
            <div>
               <Grid>
                   <Cell col={4}>
        <h4>{this.props.start}-{this.props.end}</h4>
                   </Cell>
                   
                   <Cell col={8}>
                       <h4>{this.props.post}</h4>
        <p>{this.props.name}</p>
                   </Cell>
               </Grid>
            </div>
        )
    }
}

export default Education
