import React, { Component } from 'react'
import {Tabs,Tab} from 'react-mdl'
import ReactProject from '../Projects/ReactProject'
import Unity from '../Projects/Unity';
import JavaProject from '../Projects/JavaProject'
import {motion} from 'framer-motion'
export class Projetcs extends Component {
    
    constructor(props) {
        super(props)
        this.state = { activeTab: 0 };
    }
    

   renderContent=()=>{
    
        if(this.state.activeTab==0) return <ReactProject/>
        else if (this.state.activeTab==1) return <JavaProject/>
        else if(this.state.activeTab==2) return <Unity/>
     
    }

    render() {
        return (
            <motion.div 
            initial={{opacity:0,y:'-100%'}}
            animate={{opacity:1,y:0}}
            exit={{opacity:0,y:'-100%'}}
            className="demo-tabs">
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
                    <Tab>React</Tab>
                    <Tab>Android</Tab>
                    <Tab>Unity</Tab>
                </Tabs>
                <section>
                    <div className="content">
                        {this.renderContent()}
                    </div>
                </section>
            </motion.div>    
        )
    }
}

export default Projetcs

