import React from 'react'
import { Grid, Cell,List,ListItem,ListItemAction,ListItemContent } from 'react-mdl'
import Education from './Education'
import {motion} from 'framer-motion'
function Contacts() {
    return (
        <motion.div initial={{opacity:0}}
        animate={{opacity:1}}
        exit={{opacity:0}}
        
        className="contact-body">
            <Grid className="contact-grid">
                <Cell col={6}>
                    <h2>Samul Shrestha </h2>
                    <img src="https://fiverr-res.cloudinary.com/images/t_main1,q_auto,f_auto,q_auto,f_auto/gigs/125296371/original/653cc81872119844644e33d40f5afd9bd61743b6/create-cool-cartoon-avatars.jpg"

                        alt="avatar"
                        style={{ height: '150px', width: 'auto', borderRadius: '5px' }} />
                    <p>
                   <div>
                       <strong>Date of Birth:</strong> July 14, 1997 
                       </div> 
                       <div>
                           <strong>Gender: </strong> Male               
                           </div>
                    <div>
                        <strong>Birth Place: </strong> Kathmandu, Nepal
                        </div>
                        <div> 

                    <strong>Availability</strong>: Full Time
                        </div>

            </p>

                </Cell>
                <Cell col={6}>
                    <h2>Contact Me</h2>
                    <hr />
                    <div className="contact-list">
                    <List >
                        <ListItem>
                            <ListItemContent style={{fontSize:'22px',fontFamily:'Oxygen'}} icon="person">Samul Shrestha</ListItemContent>
                        </ListItem>
                        <ListItem>
                            <ListItemContent style={{fontFamily:'Oxygen'}} icon="phone">9861601287</ListItemContent>
                        </ListItem>
                        <ListItem>
                            <ListItemContent style={{fontFamily:'Oxygen'}}icon="email">samulshrestha101@gmail.com</ListItemContent>
                        </ListItem>
                        <ListItem>
                            <ListItemContent style={{fontFamily:'Oxygen'}}icon="map">Nayabazar, Kathmandu</ListItemContent>
                        </ListItem>
                    </List>

                    </div>
                    
                </Cell>


            </Grid>

        </motion.div>
    )
}

export default Contacts
